<template>
  <div class="edit-gender">
    <van-picker
      :columns="columns"
      :default-index="defaultIndex"
      show-toolbar
      title=""
      @cancel="$emit('close')"
      @change="onchange"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
export default {
  name: 'EditGender',
  components: {},
  props: {
    gender: {
      required: true,
      type: Number
    }
  },
  data () {
    return {
      columns: ['', ''],
      defaultIndex: this.gender
    }
  },
  computed: {},
  watch: {},
  methods: {

    async onConfirm () {
      if (this.defaultIndex === this.gender) {
        this.$toast.success('')
        this.$emit('close')
        return
      }
      console.log(123)
      this.$toast.loading({
        message: '',
        forbidClick: true,
        duration: 0
      })

      try {
        await this.$axios({
          method: 'PATCH',
          url: '/app/v1_0/user/profile',
          data: {
            gender: this.defaultIndex
          }
        })
        // 
        this.$toast.success('')

        // 
        this.$emit('update:gender', this.defaultIndex)
        this.$emit('close')

      } catch (e) {
        this.$toast({
          message: '' + e,
          position: 'top'
        })
      }
    },
    onchange (vc, value, index) {
      this.defaultIndex = index
    }
  },
  created () {
  },
  mounted () {
  }
}
</script>

