<template>
  <div class="edit-name">
    <van-nav-bar
      left-arrow
      left-text=""
      right-text=""
      title=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
    <div class="field-warp">
      <van-field
        v-model="message"
        autosize
        maxlength="7"
        placeholder=""
        rows="2"
        show-word-limit
        type="textarea"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'EditName',
  components: {},
  props: {
    name: {
      required: true,
      type: String
    }
  },
  data () {
    return {
      message: this.name
    }
  },
  computed: {},
  watch: {},
  methods: {

    async onConfirm () {

      if (this.message === this.name) {
        return this.$toast({
          message: '',
          position: 'top'
        })
      }
      this.$toast.loading({
        message: '',
        forbidClick: true,
        duration: 0
      })
      try {
        await this.$axios({
          method: 'PATCH',
          url: '/app/v1_0/user/profile',
          data: {
            name: this.message
          }
        })
        // 
        this.$toast.success('')

        // 
        this.$emit('update:name', this.message)
        this.$emit('close')

      } catch (e) {
        if (e && e.response && e.response.status === 409) {
          return this.$toast.fail({
            message: '',
          })
        }
        this.$toast({
          message: '',
          position: 'top'
        })
      }
    }
  },
  created () {
  },
  mounted () {
  }
}
</script>

<style lang="less" scoped>
.field-warp {
  padding: 10px;
}

</style>
