<template>
  <div class="edit-photo">
    <img :src="previewImages" ref="image" class="preview-img" />
    <!--  -->
    <van-nav-bar
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

export default {
  name: 'EditPhoto',
  components: {},
  props: {
    previewImages: {
      require: true,
      type: String
    }
  },
  data () {
    return {
      cropper:null, // 
    }
  },
  computed: {},
  watch: {},
  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '',
        forbidClick: true,
        duration: 0
      })
      try {
        // Content-Type	multipart/form-data
        //  Content-Type	 	multipart/form-data  FormData   
        //  {} 
        const file = await this.getCroppedCanvas()
        const fd = new FormData()
        fd.append('photo', file)

        await this.$axios({
          method: 'PATCH',
          url: '/app/v1_0/user/photo',
          data: {
            photo: fd
          }
        })

        // 
        this.$toast.success('')
        // 
        this.$emit('update-photo', window.URL.createObjectURL(file))
        // 
        this.$emit('close')
      } catch (e) {
        this.$toast({
          message: '' + e,
          position: 'top'
        })
      }
    },
    getCroppedCanvas(){
      return new Promise(resolve => {
        this.cropper.getCroppedCanvas().toBlob(blob => {
          resolve(blob)
        })
      })
    }

  },
  created () {
  },
  mounted () {
    const image = this.$refs.image
    this.cropper = new Cropper(image, {
      viewMode:1,       // 
      dragMode:'move',  // 
      aspectRatio:1,  //  1:1 
      // autoCropArea:1,  //  
      cropBoxMovable:false,  // 
      cropBoxResizable:false, // 
      background:false,   // 
      movable:true,     //  
    })


  }
}
</script>

<style lang="less" scoped>
.field-warp {
  padding: 10px;
}
.preview-img{
  width: 100%;
  max-width: 100%;
}

.van-nav-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: unset;
}
</style>
