<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      class="app-nav-bar"
      left-arrow
      title=""
      @click-left="$router.back()"
    />
    <!--  -->
    <input
      ref="file"
      accept="image/*"
      hidden
      type="file"
      @change="onFileChange"
    />
    <van-cell-group>
      <van-cell
        center
        class="avatar"
        is-link
        title=""
        @click="$refs.file.click()"
      >
        <van-image
          :src="user.photo"
          fit="cover"
          height="34"
          round
          width="34"
        />
      </van-cell>
      <van-cell
        :value="user.name"
        is-link
        title=""
        @click="editNamePopup = true"
      />
      <van-cell
        :value="user.gender === 0 ? '' : ''"
        is-link
        title=""
        @click="editGenderPopup = true"
      />
      <van-cell
        :value="user.birthday"
        is-link
        title=""
        @click="editBirthdayPopup = true"
      />
    </van-cell-group>

    <!--  -->
    <van-popup
      v-model="editNamePopup"
      :style="{ height: '100%' }"
      class="edit-name-popup"
      position="bottom"
    >
      <!-- :name="user.name" @updata-name="user.name = $event" -->
      <!--
    v-model="user.name"
       vlaue
        :value='user.name'
        @input 
         @input="user.name = $event"
 -->
      <edit-name
        v-if="editNamePopup"
        :name.sync="user.name"
        @close="editNamePopup = false"
      />
    </van-popup>

    <!--  -->
    <van-popup
      v-model="editGenderPopup"
      :style="{ height: '40%' }"
      position="bottom"
    >
      <edit-gender
        v-if="editGenderPopup"
        :gender.sync="user.birthday"
        @close="editGenderPopup = false"
      />
    </van-popup>
    <!--  -->
    <van-popup
      v-model="editBirthdayPopup"
      :style="{ height: '40%' }"
      position="bottom"
    >
      <edit-birthday
        v-if="editBirthdayPopup"
        :birthday.sync="user.birthday"
        @close="editBirthdayPopup = false"
      />
    </van-popup>
    <!--  -->
    <van-popup
      v-model="editPhotoPopup"
      :style="{ height: '100%' }"
      class="edit-photo-popup"
      position="bottom"
    >
      <edit-photo
        v-if="editPhotoPopup"
        :previewImages="previewImages"
        @close="editPhotoPopup = false"
        @update-photo="user.photo = $event"
      />
    </van-popup>
  </div>
</template>

<script>
import EditName from './components/editName.vue'
import EditGender from './components/editGender'
import EditBirthday from './components/editBirthday.vue'
import EditPhoto from './components/editPhoto'

export default {
  name: 'UserProfile',
  components: {
    EditName,
    EditGender,
    EditBirthday,
    EditPhoto
  },
  props: {},
  data () {
    return {
      user: {}, // 
      // 
      editNamePopup: false,
      // 
      editGenderPopup: false,
      // 
      editBirthdayPopup: false,
      editPhotoPopup: false,
      previewImages: null
    }
  },
  computed: {},
  watch: {},
  methods: {
    async getUserInfo () {
      const { data } = await this.$axios({
        method: 'get',
        url: '/app/v1_0/user/profile'
      })
      this.user = data.data
    },

    onFileChange () {
      //  blod   blod
      this.previewImages = window.URL.createObjectURL(this.$refs.file.files[0])
      // 
      this.editPhotoPopup = true

      //  change  file value
      this.$refs.file.value = ''
    }
  },
  created () {
    this.getUserInfo()
  },
  mounted () {
  }
}
</script>

<style lang="less" scoped>
.avatar {
  /deep/ .van-cell__value {
    height: 37px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

.edit-name-popup {
  background: #f5f7f9;
}

.edit-photo-popup {
  background: #222222;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
